.container {
  padding: 30px;
}

.requestContainer {

  display: flex;
  flex-direction: column;
  width: 500px;
  justify-content: flex-start;
  align-items: flex-start;

  .textarea {
    resize: none;
    width: 100%;
    height: 100px;
    overflow-y: auto;
    outline: 0;
    border: solid 1px rgba(0, 0, 0,0.3);
    padding: 5px 10px;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-size: 14px;
    color: #00112E;
    border-radius: 12px;
  }

  .buttonSendRequest {
    padding: 10px 12px;
    cursor: pointer;
    background: #0092FA;
    color: #ffffff;
    border: 0;
    outline: none;

    margin-top: 8px;

    border-radius: 6px;

    transition: background .2s ease;

    &:hover {
      background: #0181DB;
    }
    &:active {
      background: #006ADB;
    }
  }
}



.error {
  font-size: 14px;
  margin-top: 8px;

  .highlight {
    color: #FF584E;
  }

  .default {
    color: #00112E;
  }
}

.changeResponseValue {
  color: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  .row {
    margin: 4px 0;
    .description {
      color: rgba(0, 0, 0, 0.7);
      font-size: 12px;
    }
    .value {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.9);
    }
  }
}

.selectResponseValue {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  .title {
    margin-bottom: 8px;
    font-weight: bold;
  }
  table {
    border-spacing: 0;
    font-size: 14px;
    th {
      padding: 10px;
      background: rgba(0, 0, 0, 0.03);
      font-weight: normal;
      border: solid 1px rgba(0, 0, 0, 0.4);
    }
    td {
      padding: 5px 8px;
      text-align: center;
    }
    tbody {
      td {
        border: dashed 1px rgba(0, 0, 0, 0.2);
      }
    }
  }
}
